import * as React from "react";
import type { HeadFC } from "gatsby";
import { Link, navigate } from "gatsby";
import AppContext from "../../context/AppContext";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Dropdown from "react-bootstrap/Dropdown";
import { WrappedParse as Parse } from "../../vendor/ParseApp";
import Spinner from "react-bootstrap/Spinner";
import {
  Trans,
  useTranslation,
  I18nextContext,
} from "gatsby-plugin-react-i18next";
import TopBar from "../topbar";
import NavBar from "../topbar/navbar";
import HowItWorks from "../section/HowItWorks";
import Footer from "../section/Footer";
import { PUBLIC_ROUTES } from "../../constant";

const CustomToggle = React.forwardRef(({ children, onClick }, ref) => (
  <a
    href=""
    ref={ref}
    onClick={(e) => {
      e.preventDefault();
      onClick(e);
    }}
  >
    {children}
    &#x25bc;
  </a>
));

const IndexPage = ({ children }) => {
  const { useEffect, useState } = React;
  const [roles, setRoles] = useState([]);

  const { t } = useTranslation();
  const [isLoading, setIsLoading] = React.useState(false);
  const [contextState, setContextState] = React.useState({
    theme: "",
    showLoading: (show) => {
      setIsLoading(show);
    },
  });
  const user = Parse?.User?.current();
  const isAuthenticated = !!user;

  useEffect(() => {
    const path = typeof window !== "undefined" ? window.location.pathname : "";
    if (PUBLIC_ROUTES.includes(path) && isAuthenticated) {
      console.log(path);
      navigate("/");
    }
    Parse.Cloud.run("getMyRole").then((e) => {
      setRoles(e);
    });
  }, []);
  return (
    <AppContext.Provider value={contextState}>
      <TopBar user={user} roles={roles}></TopBar>
      <NavBar roles={roles} username={isAuthenticated && user.get("email")} />
      <div className="main-content">
        <div className="page-content">
          <main>
            <Container fluid className="position-relative page">
              <Row>
                <Col xs={12}>
                  {children}
                  {isLoading && (
                    <div className="spotlight">
                      <Spinner animation="border" role="status">
                        <span className="visually-hidden">Loading...</span>
                      </Spinner>
                    </div>
                  )}
                </Col>
              </Row>
            </Container>
          </main>
        </div>
      </div>
      <Footer />
    </AppContext.Provider>
  );
};

export default IndexPage;

export const Head: HeadFC = () => <title>Home Page</title>;
